<template>
    <div>
       <el-link 
       href="https://vuex.vuejs.org/zh/" 
       :underline = false
       target="_blank">Vuex 
       </el-link> 
       <el-alert
           title="Vuex  Vue.js "
           description=""
           center
           :closable="false"
           effect = "dark"
           type="info">
       </el-alert>
       <el-alert
           title=" Vuex  storestore (state)"
           description="Vuex  store  store  (commit) mutation"
           type="info"
           center
           :closable="false">
       </el-alert>
       <el-collapse  >
            <el-collapse-item title="State" name="1">
                <div>Vuex  store  Vue.use(Vuex)</div>
                <div> store  store </div>
                <div> this.$store.state</div>
            </el-collapse-item>
            <el-collapse-item title="Getter" name="2">
                <div>getter store  state </div>
                <div>store.getters </div>
                <div> getter  getter </div>
            </el-collapse-item>
            <el-collapse-item title="Mutation" name="3">
               <div> Vuex  store  mutation
                   <p>mutation  (type)(handler) state </p>
               </div>
               <div> mutation handler type  store.commit </div>
               <div>this.$store.commit('handlerName')</div>
            </el-collapse-item>
            <el-collapse-item title="Action" name="4">
                <div>Action  mutation</div>
                <div>Action </div>
                <div> <el-link href = "https://vuex.vuejs.org/zh/guide/actions.html">Action</el-link></div>
            </el-collapse-item>
            <el-collapse-item title="Modules" name="5">
                <ul>
                    <li> <div>store 
Vuex  store module statemutationactiongetter</div></li>
                    <li>
                <div> 
                    <p>const moduleA = {
                    state: { ... },
                    mutations: { ... },
                    actions: { ... },
                    getters: { ... }
                    }</p></div>
                    </li>
                    <li>
                    <div><p>const store = new Vuex.Store({
                        modules: {
                            a: moduleA,
                            b: moduleB
                        }
                        })
                        </p></div>
                    </li>
                    <li>
                        <div><p>store.state.a // -> moduleA  store.state.b // -> moduleB </p></div>
                    </li>
                    <li>
                        <div><p> actionmutation  getter  mutation  action 
                         namespaced: true </p>
                        <strong>commit dispatch </strong>
                        </div>
                    </li>
                    <li>
                        <div>rootState  rootGetter </div>
                    </li>
                    <li>
                        <div><p> store.registerModule  store.state.myModule  store.state.nested.myModule </p></div>
                    </li>
                </ul>
            </el-collapse-item>
       </el-collapse>
       <div>
           <ul>
               <li> this.$store.state:{{this.$store.state.count}}</li>
               <li> this.$store.getters:{{this.$store.getters.gettersCount}}</li>
               <li @click="add">add2222</li>
               <li @click="back">back</li>
           </ul>
       </div>
       <el-link
       href="https://baijiahao.baidu.com/s?id=1618794879569468435&wfr=spider&for=pc" 
