</template>
<script>
export default {
    data () {
        return {
        }
    },
    mounted () {
        
    },
    methods: {

        add(){
            this.$store.commit('add',2222)
        },
        // 
        back(){
            this.$store.commit({
                type:'clear',
                count:1111
            })
        }
    }
}
</script>

